import products from './products-info';

const productsContainer = window.document.querySelector(
  '.products-sec__content'
);

export const productListInit = () => {
  productsContainer.innerHTML = products
    .map(
      (product) => `
	<div class="product-card ${product.desc.includes('sofa') && 'two'}  u-bg-${
        product.bgColor
      }">
							<h3 class="product-card__title">${product.name}</h3>
							<img
								class="product-card__image"
								src=${product.imgUrl}
								alt="${product.name} ${product.desc}"
							/>
							<a class="product-card__link" href="#"> More info→ </a>
						</div>
                `
    )
    .join(' ');
};
