const modal = window.document.querySelector('.modal');
const closeModalBtn = window.document.querySelector('.modal__btn--close');
const newsLettersForm = window.document.querySelector('.newsletters-form');

const TIME_OUT = 2000;

const showModal = () => modal.classList.add('show');

const hideModal = () => modal.classList.remove('show');

newsLettersForm.addEventListener('submit', (e) => {
  e.preventDefault();

  hideModal();
});

export const modalInit = () => {
  window.setTimeout(showModal, TIME_OUT);
  modal.addEventListener('click', (e) => {
    const targetEle = e.target;
    switch (targetEle) {
      case modal:
      case closeModalBtn:
        hideModal();
        break;

      default:
        break;
    }
  });
};
