const togglerBtn = window.document.querySelector('.toggler-btn');
const headerNav = window.document.querySelector('.header__nav');

export const togglerBtnInit = () =>
  togglerBtn.addEventListener('click', () => {
    togglerBtn.classList.toggle('open');
    headerNav.classList.toggle('open');
    window.setTimeout(() => {
      togglerBtn.blur();
    }, 1000);
  });
