import products from './products-info';

// Chaching needed Dom Elements
const slider = window.document.querySelector('.slider');
const slidesContainer = window.document.querySelector('.slider__slides');
let slides;
let sliderNavDots;
const indicatorslist = window.document.querySelector(
  '.slider__navigator__indicators-list'
);
const sliderArrowPrev = window.document.querySelector(
  '.slider__navigator__arrow--prev'
);
const sliderArrowNext = window.document.querySelector(
  '.slider__navigator__arrow--next'
);

// Local variable for main module
const slidesNum = products.length;

// eslint-disable-next-line prefer-const
let prevSlideIndex = 0;
// eslint-disable-next-line prefer-const
let currSlideIndex = 1;
// eslint-disable-next-line prefer-const
let nextSlideIndex = 2;

let timerId;
const AUTOPLAYTIME = 5000;

const classesToRemove = [
  'slider__slide--prev',
  'slider__slide--curr',
  'slider__slide--next',
];

const slideNumberCheck = (slideNum) => {
  if (slideNum === slidesNum) return 0;
  if (slideNum < 0) return slidesNum - 1;
  return slideNum;
};

const resetClasses = () => {
  slides[prevSlideIndex].classList.remove(...classesToRemove);
  slides[currSlideIndex].classList.remove(...classesToRemove);
  slides[nextSlideIndex].classList.remove(...classesToRemove);
};

// create Slides items Elements
const createSlides = () => {
  products.forEach((slide) => {
    slidesContainer.insertAdjacentHTML(
      'beforeend',
      `
      	<div class="slider__slide u-bg-${slide.bgColor}">
							<figure class="slider__slide__fig container">
								<img
									class="slider__slide__fig__image"
									src=${slide.imgUrl}

                   alt="${slide.name} Avatar"
								/>
								<figcaption class="slider__slide__fig__info">
									<p class="slider__slide__fig__info__des">${slide.desc}</p>
									<h3 class="slider__slide__fig__info__name">${slide.name}</h3>
									<strong class="slider__slide__fig__info__price">$${slide.price}</strong>
									<button class="slider__slide__fig__info__btn">buy</button>
								</figcaption>
							</figure>
						</div>`
    );
  });

  slides = window.document.querySelectorAll('.slider__slide');
};

//  create create Indicators Elements
const createIndicators = () => {
  products.forEach((_, index) => {
    indicatorslist.insertAdjacentHTML(
      'beforeend',
      `<li
      class="slider__navigator__indicators-list__item"
      data-slide-num="${index}"
    ></li>`
    );
  });
  sliderNavDots = window.document.querySelectorAll(
    '.slider__navigator__indicators-list__item'
  );
};

const goToSlide = (slideNum) => {
  resetClasses();
  prevSlideIndex = slideNumberCheck(slideNum - 1);
  currSlideIndex = slideNum;
  nextSlideIndex = slideNumberCheck(slideNum + 1);

  slides[prevSlideIndex].classList.add('slider__slide--prev');
  slides[currSlideIndex].classList.add('slider__slide--curr');
  slides[nextSlideIndex].classList.add('slider__slide--next');

  // eslint-disable-next-line no-use-before-define
  activeDot();
};

const nextSlide = () => goToSlide(slideNumberCheck(currSlideIndex + 1));

const prevSlide = () => goToSlide(slideNumberCheck(currSlideIndex - 1));

const activeDot = () => {
  sliderNavDots.forEach((slide) => slide.classList.remove('active'));
  sliderNavDots[currSlideIndex].classList.add('active');
};

//  Keyboard arrows Events
document.addEventListener('keyup', (e) => {
  switch (e.key) {
    case 'ArrowRight':
      nextSlide();
      break;
    case 'ArrowLeft':
      prevSlide();
      break;
    default:
      break;
  }
});

//  autoplay
const autoPlay = () => (timerId = window.setInterval(nextSlide, AUTOPLAYTIME));

const pauseAutoSlide = () => window.clearInterval(timerId);

slider.addEventListener('mouseenter', pauseAutoSlide);
slider.addEventListener('mouseleave', autoPlay);

//  mouseDragable Function
const mouseDragable = () => {
  let isDown = false;
  let isDrag = false;
  let startX = 0;
  let endX = 0;

  slider.addEventListener('mousedown', (e) => {
    isDown = true;
    slider.classList.add('slider--active');
    startX = e.pageX;
  });

  slider.addEventListener('mouseleave', () => {
    isDown = false;
    // slider.classList.remove("slider--active");
  });

  slider.addEventListener('mouseup', () => {
    isDown = false;
    // slider.classList.remove("slider--active");
    if (isDrag === true) {
      if (endX - startX > 0) {
        prevSlide();
      } else {
        nextSlide();
      }

      isDrag = false;
    }
  });

  slider.addEventListener('mousemove', (e) => {
    if (!isDown) return;
    e.preventDefault();
    // slider.classList.add("slider--active");
    isDrag = true;
    endX = e.pageX;
  });
};

//  Add Events
// Add click event to generator button to get random quote and display it.
sliderArrowPrev.addEventListener('click', prevSlide);

sliderArrowNext.addEventListener('click', nextSlide);

indicatorslist.addEventListener('click', (e) => {
  const clickedItem = e.target;
  if (
    clickedItem.classList.contains('slider__navigator__indicators-list__item')
  ) {
    const currentDotIndex = Number(clickedItem.dataset.slideNum);
    goToSlide(currentDotIndex);
  }
});

//  Slider Init function
export const sliderInit = () => {
  createSlides();
  createIndicators();
  goToSlide(0);
  activeDot();
  autoPlay();
  mouseDragable();
};
