import symbolDefs from 'url:../assets/icons/symbol-defs.svg';

// Chaching needed Dom Elements
const internetNotify = window.document.querySelector('.internet-notify');
let isNotifyShow = false;
const TIME_OUT = 10000;

// Helper Variables to store html content of internet notify in off & on
const disableHtmlContent = `
                            <svg class="internet-notify__icon internet-notify__icon--off icon icon-wifi_off">
                              <use
                                xlink:href="${symbolDefs}#icon-wifi_off"
                              ></use>
                            </svg>

                            <p class="internet-notify__message">You are currently offline.</p>

                            <button class="internet-notify__btn internet-notify__btn-reload">
                              <svg class="icon icon-refresh">
                                <use
                                  xlink:href="${symbolDefs}#icon-refresh"
                                ></use>
                              </svg>
                            </button>
                         `;

const enableHtmlContent = `
                          <svg class="internet-notify__icon internet-notify__icon--on icon icon-wifi">
                            <use xlink:href="${symbolDefs}#icon-wifi"></use>
                          </svg>

                          <p class="internet-notify__message">Your connection was restored</p>

                          <button class="internet-notify__btn internet-notify__btn--close">
                            <svg class="icon icon-clear">
                              <use
                                xlink:href="${symbolDefs}#icon-clear"
                              ></use>
                            </svg>
                          </button>
                        `;

const hideNotify = () => {
  isNotifyShow = false;
  internetNotify.classList.add('hidden');
};

const showNotify = (notifyStatus) => {
  isNotifyShow = true;

  internetNotify.innerHTML =
    notifyStatus === 'on' ? enableHtmlContent : disableHtmlContent;
  internetNotify.classList.remove('hidden');

  window.console.log(
    notifyStatus === 'on'
      ? "✔ Network is on Now, let's start continue your work"
      : '❌ Network is off Now, please check connection.'
  );

  setTimeout(hideNotify, TIME_OUT);
};

const notifyHandler = (notifyStatus) => {
  if (isNotifyShow) {
    hideNotify();
    setTimeout(() => showNotify(notifyStatus), TIME_OUT / 10);
  } else {
    showNotify(notifyStatus);
  }
};

export const internetNotifyInit = () => {
  // Add Event offline to handle internet notify incase be offline
  window.addEventListener('offline', () => notifyHandler('off'));

  // Add Event online to handle internet notify incase be online.
  window.addEventListener('online', () => notifyHandler('on'));

  internetNotify.addEventListener('click', (e) => {
    const targetEelement = e.target;

    if (targetEelement.classList.contains('internet-notify__btn-reload')) {
      console.log('reload');
      window.location.reload();
    } else if (
      targetEelement.classList.contains('internet-notify__btn--close')
    ) {
      console.log('close');
      hideNotify();
    }
  });
};
