import { scrollToTopBtn } from './scroll-to-top-button';

const heroSec = document.querySelector('.hero-sec');
const header = document.querySelector('.header');
let headerHeight = header.getBoundingClientRect().height;
//  show & hidden  scroll to top button Using : Intersection Observer API
const obs = new IntersectionObserver(
  (entries) => {
    const ent = entries[0];
    headerHeight = header.getBoundingClientRect().height;

    if (ent.isIntersecting === false) {
      scrollToTopBtn.classList.add('show');
      header.classList.add('sticky');
    } else {
      scrollToTopBtn.classList.remove('show');
      header.classList.remove('sticky');
    }
  },
  {
    // In the viewport
    root: null,
    threshold: 0,
    rootMargin: `-${headerHeight * 1.5}px`,
  }
);

export const intersectionObserverInit = () => obs.observe(heroSec);
